<template>
  <v-container fluid>
    <v-form v-model="valid" ref="form">
      <v-text-field
        v-model="form.name"
        label="姓名(中文)"
        :rules="[createRules('请输入姓名', Boolean)]"
      />
      <v-text-field v-model="form.alias" label="姓名(英文/花名)" />
      <v-text-field
        v-model="form.certificate_code"
        label="身份证号码"
        :rules="[validCertCode]"
      />
      <v-select
        v-model="form.gender"
        label="性别"
        :items="select.sex.items"
        :rules="[createRules('请选择性别', Boolean)]"
      />
      <v-text-field
        v-model="form.nation"
        label="民族"
        :rules="[createRules('请输入民族', Boolean)]"
      />
      <birthday-picker
        v-model="form.ibirth"
        label="身份证出生日期"
        :rules="[createRules('请选择出生日期', Boolean)]"
      />
      <birthday-picker
        v-model="form.birth"
        label="实际出生日期"
        :rules="[createRules('请选择出生日期', Boolean)]"
      />
      <v-text-field
        v-model="form.age"
        label="年龄"
        :rules="[createRules('请输入年龄', Boolean)]"
      />
      <v-text-field
        v-model="form.address"
        label="身份证住址"
        :rules="[createRules('请输入身份证住址', Boolean)]"
      />
      <birthday-picker
        v-model="form.expire_start_date"
        label="身份证有效期限起始时间"
        :rules="[createRules('请选择身份证有效期限起始时间', Boolean)]"
      />
      <birthday-picker
        v-model="form.expire_end_date"
        label="身份证有效期限终止时间"
        :rules="[createRules('请选择身份证有效期限终止时间', Boolean)]"
      />
      <v-text-field
        v-model="form.authority"
        label="签发机关"
        :rules="[createRules('请输入签发机关', Boolean)]"
      />
      <v-text-field
        v-model="form.authority"
        label="本人联系方式"
        :rules="[createRules('请输入本人联系方式', Boolean)]"
      />
      <v-select
        v-model="form.household_type"
        label="户口性质"
        :items="select.household.items"
        :rules="[createRules('请选择性别', Boolean)]"
      />
      <v-select
        v-model="form.policital_status"
        label="政治面貌"
        :items="select.policital.items"
        :rules="[createRules('请选择政治面貌', Boolean)]"
      />
      <v-select
        v-model="form.marital_status"
        label="婚姻状况"
        :items="select.marital.items"
        :rules="[createRules('请选择婚姻状况', Boolean)]"
      />
      <v-text-field
        v-model="form.place"
        label="户口信息"
        :rules="[createRules('请输入户口信息', Boolean)]"
      />
      <ChildrenInfo v-model="form.children" />
      <v-text-field
        v-model="form.emergency_name"
        label="紧急联系人"
        :rules="[createRules('请填写紧急联系人', Boolean)]"
      />
      <v-text-field
        v-model="form.emergency_relation"
        label="紧急联系人"
        :rules="[createRules('请填写与紧急联系人关系', Boolean)]"
      />
      <v-text-field
        v-model="form.emergency_phone"
        label="紧急联系人"
        :rules="[createRules('请填写紧急联系人电话', Boolean)]"
      />
      <v-text-field
        v-model="form.present_addr"
        label="现住址"
        :rules="[createRules('请填写现住址', Boolean)]"
      />
      <v-text-field v-model="form.remark" label="备注" />
      <v-row>
        <v-btn @click="$emit('prev')">上一步</v-btn>
        <v-spacer />
        <v-btn color="primary" @click="next">下一步</v-btn>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
import * as regs from '@/lib/regexp'
import formMixin from '@/mixins/form'
import ChildrenInfo from './components/ChildrenInfo'
export default {
  name: 'BaseInformation',

  mixins: [formMixin],

  components: {
    ChildrenInfo,
  },

  data() {
    return {
      valid: true,
      form: {
        name: '', // 身份证姓名
        alias: '', // 别名
        certificate_code: '430626198610242115', // 证件号码
        gender: '', // 性别
        ibirth: '', // 身份证出生日期
        birth: '', // 真实出生日期
        age: '', // 年龄
        address: '', // 身份证住址
        expire_start_date: '', // 身份证有效起始时间
        expire_end_date: '', // 身份证有效结束时间
        authority: '', // 签发机关
        contact: '', // 本人联系方式
        household_type: '', // 户口性质
        policital_status: '', // 政治面貌
        marital_status: '', // 婚姻状况
        children: [], // 子女信息
        place: '', // 户口信息
        emergency_name: '', // 紧急联系人姓名
        emergency_relation: '', // 紧急联系人关系
        emergency_phone: '', // 紧急联系人电话
        present_addr: '', // 现住址
        remark: '', // 备注
      },
    }
  },

  methods: {
    next() {
      const valid = this.$refs.form.validate()
      if (valid) {
        this.$emit('next', this.form)
      }
    },
    validCertCode(v) {
      return (!!v && regs.REG_IDCARDNUMBER.test(v)) || '请输入有效的证件号码'
    },
  },
}
</script>

<style lang="scss" scoped></style>
