<template>
  <v-container fluid>
    <v-dialog v-model="dialog" persistent>
      <template v-slot:activator="{ on }">
        <v-row justify="space-between">
          <span class="title">子女信息</span>
          <v-spacer />
          <v-btn v-on="on" class="mx-2" fab dark small color="primary">
            <v-icon dark>mdi-plus</v-icon>
          </v-btn>
        </v-row>
      </template>
      <v-card>
        <v-card-title>
          <span class="headline">子女信息表</span>
        </v-card-title>
        <v-card-text>
          <v-form v-model="valid" ref="form">
            <v-text-field
              v-model="form.name"
              label="姓名"
              :rules="[createRules('请输入姓名', Boolean)]"
            />
            <v-select
              v-model="form.gender"
              label="性别"
              :items="select.sex.items"
              :rules="[createRules('请选择性别', Boolean)]"
            />
            <birthday-picker
              v-model="form.birth"
              label="出生年月"
              :rules="[createRules('请选择出生年月', Boolean)]"
            />
          </v-form>
        </v-card-text>
        <v-card-actions>
          <v-spacer />
          <v-btn color="blue darken-1" text @click="cancel">取消</v-btn>
          <v-btn color="blue darken-1" text @click="confirm">确定</v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <ChildrenList :value="value" @input="$emit('input', $event)" @edit="edit" />
    <v-divider class="mt-2" />
  </v-container>
</template>

<script>
import formMixin from '@/mixins/form'
import ChildrenList from './ChildrenList'
const initForm = () => ({
  name: '',
  gender: '',
  birth: '',
})
export default {
  name: 'ChildrenInfo',

  mixins: [formMixin],

  props: {
    value: {
      type: Array,
      default: () => [],
    },
  },

  components: {
    ChildrenList,
  },

  data() {
    return {
      form: initForm(),
      valid: true,
      dialog: false,
      row: null,
    }
  },

  methods: {
    edit(value) {
      this.row = value
      this.form = { ...value }
      this.dialog = true
    },
    confirm() {
      const valid = this.$refs.form.validate()
      if (valid) {
        const value = [...this.value]
        if (this.row) {
          const index = value.indexOf(this.row)
          value.splice(index, 1, { ...this.form })
          this.row = null
        } else {
          value.push({ ...this.form })
        }
        this.$refs.form.reset()
        this.$emit('input', value)
        this.form = initForm()
        this.dialog = false
      }
    },
    cancel() {
      this.$refs.form.reset()
      if (this.row) {
        this.form = initForm()
        this.row = null
      }
      this.dialog = false
    },
  },
}
</script>

<style lang="scss" scoped></style>
