<template>
  <v-container>
    <v-form v-model="valid" ref="form">
      <EducationCard
        v-for="(item, index) in params.list"
        :key="item.id || index"
        :value="item"
        :title="`学习经历-${index + 1}`"
        ref="cards"
        @input="params.list.splice(index, 1, $event)"
        @remove="remove(index)"
      >
        <template v-slot:actions>
          <v-btn
            v-if="index === params.list.length - 1"
            text
            color="primary"
            class="pa-0"
            @click="addMore"
            >添加更多</v-btn
          >
        </template>
      </EducationCard>
      <v-form-item
        v-model="params.highest_edu"
        type="select"
        :items="select.education.items"
        label="最高学历"
        required
      />
      <v-list>
        <v-list-item
          v-for="(value, index) in params.certificates"
          :key="index"
          class="pa-0"
        >
          <v-form-item
            v-model="params.certificates[0]"
            type="text"
            :label="`证书${index + 1}`"
          >
            <template v-slot:append-outer>
              <v-btn
                text
                icon
                color="primary"
                @click="params.certificates.push('')"
              >
                <v-icon>mdi-plus</v-icon>
              </v-btn>
              <v-btn text icon color="error" @click="removeCert(index)">
                <v-icon>mdi-minus</v-icon>
              </v-btn>
            </template>
          </v-form-item>
        </v-list-item>
      </v-list>
    </v-form>
    <v-row>
      <v-btn @click="$emit('prev')">上一步</v-btn>
      <v-spacer />
      <v-btn color="primary" @click="next">下一步</v-btn>
    </v-row>
  </v-container>
</template>

<script>
import EducationCard from './components/EducationCard'
import { initEduInfo } from './components/utils.js'
import formMixin from '@/mixins/form'
export default {
  name: 'EducationInformation',

  mixins: [formMixin],

  components: {
    EducationCard,
  },

  data() {
    return {
      params: {
        highest_edu: '',
        list: [initEduInfo()],
        certificates: [''],
      },
      valid: true,
    }
  },

  methods: {
    addMore() {
      this.params.list.push(initEduInfo())
    },
    validate() {
      return (
        this.$refs.cards.every(item => item.validate()) &&
        this.$refs.form.validate()
      )
    },
    remove(index) {
      if (this.params.list.length === 1) {
        this.params.list = [initEduInfo()]
      } else {
        this.params.list.splice(index, 1)
      }
    },
    removeCert(index) {
      const certs = this.params.certificates
      if (certs.length === 1) {
        certs.splice(index, 1, '')
      } else {
        certs.splice(index, 1)
      }
    },
    next() {
      if (this.validate()) {
        this.$emit('next', this.params)
      }
    },
  },
}
</script>

<style lang="scss" scoped></style>
