<template>
  <v-container fluid>
    <v-form v-model="valid" ref="form">
      <base-label
        label="1、自与我司签订合同期内是否有在其他公司有兼职工作或持有股份等？"
      >
        <YesOrNo v-model="params.qs1" />
      </base-label>
      <base-label
        label="2、自与我司签订合同期内是否涉嫌正在调查的违法违规或犯罪行为？"
      >
        <YesOrNo v-model="params.qs2" />
      </base-label>
      <base-label label="3、是否有亲属或朋友在本公司任职？">
        <YesOrNo v-model="params.qs3" />
        <v-form-item
          v-if="params.qs3 === 1"
          v-model="params.friends"
          type="text"
          label="请填写亲友姓名"
          placeholder="如有多位请用逗号分隔"
        />
      </base-label>
      <base-label label="4、是否在竞业限制期内？">
        <YesOrNo v-model="params.qs4" />
      </base-label>
      <base-label
        label="5、是否曾被依法追究刑事责任、或被拘留、或被劳教、或被收容、或被强制戒毒？"
      >
        <YesOrNo v-model="params.qs5" />
      </base-label>
      <base-label label="6、目前是否患有任何重大疾病或/传染性疾病?">
        <YesOrNo v-model="params.qs6" />
      </base-label>
      <base-label label="7、是否受过鉴定登记的工伤？">
        <YesOrNo v-model="params.qs7" />
      </base-label>
      <base-label label="8、其他需说明的问题（有/无填写）">
        <v-form-item v-model="params.remark" type="text" />
      </base-label>
      <p>
        本人已对《新员工入职登记表》的内容进行了认真、仔细阅读，并确认所有填报内容真实、准确、完整，无任何虚假记载、误导性陈述或重大遗漏。
        本人郑重承诺：如填报内容存在虚假记载、误导性陈述或重大遗漏，本人将承担一切责任。
      </p>
      <v-row>
        <v-col cols="6" class="d-flex">
          <span>姓名：</span>
          <SignatureDialog :value="params.sign" @save="params.sign = $event" />
        </v-col>
        <v-col cols="6" class="d-flex">
          <span>日期：</span>
          <SignatureDialog :value="params.date" @save="params.date = $event" />
        </v-col>
      </v-row>
    </v-form>
    <v-row>
      <v-btn @click="$emit('prev')">上一步</v-btn>
      <v-spacer />
      <v-btn color="primary" @click="next">下一步</v-btn>
    </v-row>
  </v-container>
</template>

<script>
import YesOrNo from './components/YesOrNo'
export default {
  name: 'Questions',

  components: {
    YesOrNo,
  },

  data() {
    return {
      params: {
        qs1: '',
        qs2: '',
        friends: '',
        sign: '',
        date: '',
      },
      valid: true,
    }
  },

  methods: {
    validate() {
      return this.$refs.form.validate()
    },
    next() {
      if (this.validate()) {
        this.$emit('next', this.params)
      }
    },
    openDialog() {
      this.$refs.dialog.open(this.params.sign)
    },
  },
}
</script>

<style lang="scss" scoped></style>
