<template>
  <v-card flat width="100%" class="mb-1">
    <v-card-title>{{ title }}</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form">
        <v-form-item
          v-model="form.school_name"
          type="text"
          label="学校名"
          required
        />
        <v-form-item
          v-model="form.date"
          type="format-date-range"
          label="时间范围"
          separator="~"
          required
        />
        <v-radio-group
          v-model="form.location"
          label="国内/国外"
          row
          :rules="[createRules('请选择国内/国外', v => !isNaN(v))]"
        >
          <v-radio label="国内" :value="0" />
          <v-radio label="国外" :value="1" />
        </v-radio-group>
        <v-form-item
          v-model="form.profession"
          type="text"
          label="专业"
          required
        />
        <v-form-item
          v-model="form.education"
          type="select"
          :items="select.education.items"
          label="学历"
          required
        />
        <v-form-item
          v-model="form.certificate_code"
          type="text"
          label="学历证书编号"
          required
        />
        <v-form-item
          v-model="form.degree"
          type="select"
          :items="select.degree.items"
          label="学位"
        />
        <v-form-item
          v-model="form.diploma_no"
          v-if="form.location === 0 && form.degree"
          type="text"
          label="学位证书编号"
        />
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer />
      <v-btn text color="error" class="pa-0" @click="$emit('remove')"
        >删除</v-btn
      >
      <slot name="actions" />
    </v-card-actions>
  </v-card>
</template>

<script>
import formMixin from '@/mixins/form'
import { initEduInfo } from './utils.js'
export default {
  name: 'EducationCard',

  mixins: [formMixin],

  props: {
    value: Object,
    title: String,
  },

  watch: {
    value: {
      handler(value) {
        this.form = { ...value }
      },
      immediate: true,
      deep: true,
    },
  },

  data() {
    return {
      valid: true,
      form: initEduInfo(),
      validate: () => this.$refs.form.validate(),
    }
  },
}
</script>

<style lang="scss" scoped></style>
