<template>
  <v-container fluid>
    <v-form v-model="valid" ref="form">
      <date-picker-sheet
        v-model="params.first_working_date"
        type="month"
        label="首次工作时间"
      />
      <WorkExperienceCard
        v-for="(item, index) in params.works"
        :key="item.id || index"
        :value="item"
        :title="`工作经历-${index + 1}`"
        ref="cards"
        @input="params.works.splice(index, 1, $event)"
        @remove="remove(index)"
      >
        <template v-slot:actions>
          <v-btn
            v-if="index === params.works.length - 1"
            text
            color="primary"
            class="pa-0"
            @click="addMore"
            >添加更多</v-btn
          >
        </template>
      </WorkExperienceCard>
      <v-row>
        <v-btn @click="$emit('prev')">上一步</v-btn>
        <v-spacer />
        <v-btn color="primary" @click="next">下一步</v-btn>
      </v-row>
    </v-form>
  </v-container>
</template>

<script>
import WorkExperienceCard from './components/WorkExperienceCard'
import { initWorkInfo } from './components/utils.js'
export default {
  name: 'WorkExperience',

  components: {
    WorkExperienceCard,
  },

  data() {
    return {
      valid: true,
      params: {
        first_working_date: '',
        works: [initWorkInfo()],
      },
    }
  },

  methods: {
    addMore() {
      this.params.works.push(initWorkInfo())
    },
    validate() {
      return (
        this.$refs.cards.every(item => item.validate()) &&
        this.$refs.form.validate()
      )
    },
    remove(index) {
      if (this.params.works.length === 1) {
        this.params.works = [initWorkInfo()]
      } else {
        this.params.works.splice(index, 1)
      }
    },
    removeCert(index) {
      const certs = this.params.certificates
      if (certs.length === 1) {
        certs.splice(index, 1, '')
      } else {
        certs.splice(index, 1)
      }
    },
    next() {
      if (this.validate()) {
        this.$emit('next', this.params)
      }
    },
  },
}
</script>

<style lang="scss" scoped></style>
