<template>
  <v-card flat width="100%" class="mb-1">
    <v-card-title>{{ title }}</v-card-title>
    <v-card-text>
      <v-form v-model="valid" ref="form">
        <v-form-item
          v-model="form.company_name"
          type="text"
          label="工作单位"
          required
        />
        <v-form-item
          v-model="form.date"
          type="date-picker-sheet"
          label="在职日期"
          required
        />
        <v-form-item
          v-model="form.position"
          type="text"
          label="职位"
          required
        />
        <v-form-item
          v-model="form.education"
          type="select"
          :items="select.education.items"
          label="学历"
          required
        />
        <v-form-item
          v-model="form.certifier"
          type="text"
          label="证明人"
          required
        />
        <v-form-item
          v-model="form.certifier_phone"
          type="text"
          label="证明人电话"
          required
        />
      </v-form>
    </v-card-text>
    <v-card-actions>
      <v-spacer />
      <v-btn text color="error" class="pa-0" @click="$emit('remove')"
        >删除</v-btn
      >
      <slot name="actions" />
    </v-card-actions>
  </v-card>
</template>

<script>
import formMixin from '@/mixins/form'
import { initWorkInfo } from './utils.js'
export default {
  name: 'EducationCard',

  mixins: [formMixin],

  props: {
    value: Object,
    title: String,
  },

  watch: {
    value: {
      handler(value) {
        this.form = { ...value }
      },
      immediate: true,
      deep: true,
    },
  },

  data() {
    return {
      valid: true,
      form: initWorkInfo(),
      validate: () => this.$refs.form.validate(),
    }
  },
}
</script>

<style lang="scss" scoped></style>
