<template>
  <div class="home">
    <v-stepper v-model="step" vertical>
      <v-stepper-step step="1" :complete="step > 1">基本信息</v-stepper-step>
      <v-stepper-content step="1">
        <BaseInformation @next="goto('next')" @prev="goto('prev')" />
      </v-stepper-content>

      <v-stepper-step step="2" :complete="step > 2">学习经历</v-stepper-step>
      <v-stepper-content step="2">
        <EducationInformation @next="goto('next')" @prev="goto('prev')" />
      </v-stepper-content>

      <v-stepper-step step="3" :complete="step > 3">工作经历</v-stepper-step>
      <v-stepper-content step="3">
        <WorkExperience @next="goto('next')" @prev="goto('prev')" />
      </v-stepper-content>

      <v-stepper-step step="4" :complete="step > 4">其他</v-stepper-step>
      <v-stepper-content step="4">
        <v-container fluid>
          <v-form>
            <v-form-item
              v-model="params.card_no"
              label="银行卡号"
              type="text"
              type-attr="number"
              required
            />
          </v-form>
          <v-row>
            <v-btn @click="step--">上一步</v-btn>
            <v-spacer />
            <v-btn color="primary" @click="step++">下一步</v-btn>
          </v-row>
        </v-container>
      </v-stepper-content>

      <v-stepper-step step="5" :complete="step > 5">本人声明</v-stepper-step>
      <v-stepper-content step="5">
        <Questions @next="goto('next')" @prev="goto('prev')" />
      </v-stepper-content>
    </v-stepper>
  </div>
</template>

<script>
import BaseInformation from './components/BaseInformation'
import EducationInformation from './components/EducationInformation'
import WorkExperience from './components/WorkExperience'
import Questions from './components/Questions'
export default {
  name: 'home',
  components: {
    BaseInformation,
    EducationInformation,
    WorkExperience,
    Questions,
  },

  data() {
    return {
      step: 4,
      params: {
        card_no: '',
        qs1: '',
      },
    }
  },

  methods: {
    goto(type) {
      switch (type) {
        case 'next':
          this.step++
          break
        case 'prev':
          this.step--
          break
        case 'finish':
          this.step++
          break

        default:
          break
      }
    },
  },
}
</script>
