<template>
  <v-row dense>
    <v-col
      v-for="(item, index) in value"
      :key="item.id || index"
      cols="12"
      sm="4"
    >
      <v-card outlined tile>
        <v-card-title class="headline">{{ item.name }}</v-card-title>
        <v-card-subtitle
          >出生年月：{{ item.birth }} 性别：{{
            select.sex.find(item.gender, 'text')
          }}</v-card-subtitle
        >
        <v-card-actions>
          <v-spacer />
          <v-btn text color="primary" @click="$emit('edit', item)">编辑</v-btn>
          <v-btn text color="red" @click="remove(item)">移除</v-btn>
        </v-card-actions>
      </v-card>
    </v-col>
  </v-row>
</template>

<script>
import * as select from '@/lib/select'
export default {
  name: 'ChildrenList',

  props: {
    value: {
      type: Array,
      default: () => [],
    },
  },

  data() {
    return {
      select,
    }
  },

  methods: {
    remove(row) {
      this.$emit(
        'input',
        this.value.filter(item => item !== row),
      )
    },
  },
}
</script>

<style lang="scss" scoped></style>
